import Vue from 'vue';
import Router from 'vue-router';
import Hello from '@/components/Hello';
import Word from '@/views/word';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/word',
      name: 'Word',
      component: Word
    }
  ]
});



